import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { ControlContainer, FormControl, FormGroup } from '@angular/forms';
import { EQimaInputGroupPlacement, EQimaInputSize } from '@qima/ngx-qima';

@Component({
  selector: 'app-general-information',
  templateUrl: './general-information.component.html',
  styleUrls: ['./general-information.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class GeneralInformationComponent implements OnInit {
  public readonly title: string = 'BOOKS.GENERAL_INFORMATION.TITLE';
  public form: FormGroup | undefined = undefined;
  public readonly qimaInputGroupPlacement: EQimaInputGroupPlacement = EQimaInputGroupPlacement.HORIZONTAL;
  public readonly bookingMethodInputLabel: string = 'BOOKS.GENERAL_INFORMATION.BOOKING_METHOD';
  public readonly reInspectionInputLabel: string = 'BOOKS.GENERAL_INFORMATION.THE_INSPECTION_IS_A';
  public readonly refNoInputLabel: string = 'BOOKS.GENERAL_INFORMATION.BOOKING_REFERENCE_NUMBER';
  public readonly refNoInputSize: EQimaInputSize = EQimaInputSize.MEDIUM;
  // TODO create group-radio component then remove the following
  public isQuick: FormControl = new FormControl(true);
  public isNotQuick: FormControl = new FormControl(false);
  public isFirstInspection: FormControl = new FormControl(true);
  public isReInspection: FormControl = new FormControl(false);

  public constructor(private readonly _controlContainer: ControlContainer) {}

  public ngOnInit(): void {
    this.form = <FormGroup>this._controlContainer.control;
  }
}
