import { Component, ChangeDetectionStrategy, Input } from '@angular/core';
import { EQimaIconName } from '@qima/ngx-qima';

@Component({
  selector: 'app-form-label',
  templateUrl: './form-label.component.html',
  styleUrls: ['./form-label.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class FormLabelComponent {
  /**
   * @description
   * The label for display
   * @type {boolean}
   * @default false
   */
  @Input('formLabelLabel')
  public label: string = '';

  /**
   * @description
   * Show or hide the required icon
   * @type {boolean}
   * @default false
   */
  @Input('formLabelIsShowRequired')
  public isShowRequired: boolean = false;

  /**
   * @description
   * Show or hide the info icon
   * @type {boolean}
   * @default false
   */
  @Input('formLabelIsShowInfo')
  public isShowInfo: boolean = false;

  public infoIconName: EQimaIconName = EQimaIconName.INFO;
}
