import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-pre-shipment-inspection',
  templateUrl: './pre-shipment-inspection.component.html',
  styleUrls: ['./pre-shipment-inspection.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class PreShipmentInspectionComponent implements OnInit {
  public psiForm: FormGroup = this._formBuilder.group({
    // We need to uniformly handle all the boolean values from the background
    // (Yes/No True/False number(0/1) to true/false)
    generalInfomation: this._formBuilder.group({
      bookingMethod: 'Yes', // Yes No
      reInspection: 'No', // Yes No
      refNo: ['', Validators.required],
    }),
    supplierInformation: this._formBuilder.group({}),
    expectedDates: this._formBuilder.group({
      inspectionDate: '',
      shipmentDate: '',
    }),
  });

  public constructor(private readonly _formBuilder: FormBuilder) {}

  public ngOnInit(): void {
    // TODO
    console.log('init');
  }

  public save(): void {
    // TODO
    console.log('submit');
  }
}
