import { Component, ChangeDetectionStrategy, OnInit } from '@angular/core';
import { ControlContainer, FormGroup } from '@angular/forms';

@Component({
  selector: 'app-supplier-information',
  templateUrl: './supplier-information.component.html',
  styleUrls: ['./supplier-information.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class SupplierInformationComponent implements OnInit {
  public readonly title: string = 'BOOKS.SUPPLIER_INFORMATION.TITLE';
  public form: FormGroup | undefined = undefined;
  public readonly supplierLabel: string = 'BOOKS.SUPPLIER_INFORMATION.SUPPLIER';

  public constructor(private readonly _controlContainer: ControlContainer) {}

  public ngOnInit(): void {
    this.form = <FormGroup>this._controlContainer.control;
  }
}
