import { ChangeDetectionStrategy, Component, Input } from '@angular/core';
import { EQimaButtonIconPosition, EQimaButtonSize, EQimaButtonStyle, EQimaIconName } from '@qima/ngx-qima';

@Component({
  selector: 'app-supplier-selector',
  templateUrl: './supplier-selector.component.html',
  styleUrls: ['./supplier-selector.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class SupplierSelectorComponent {
  /**
   * @description
   * Disable or enable the select add supplier button
   * @type {boolean}
   * @default false
   */
  @Input('appSupplierSelectorIsDisable')
  public isDisabled: boolean = false;

  public readonly supplierSelectorLabel: string = 'BOOKS.SUPPLIER_INFORMATION.SELECT_ADD';
  public readonly addIconName: EQimaIconName = EQimaIconName.ADD;
  public readonly buttonIconPosition: EQimaButtonIconPosition = EQimaButtonIconPosition.LEFT;
  public readonly buttonSize: EQimaButtonSize = EQimaButtonSize.LARGE;
  public readonly buttonStyle: EQimaButtonStyle = EQimaButtonStyle.TERTIARY;

  public onClick(): void {
    // TODO
  }
}
