import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { ControlContainer, FormGroup } from '@angular/forms';
import { EQimaInputSize } from '@qima/ngx-qima';

@Component({
  selector: 'app-expected-dates',
  templateUrl: './expected-dates.component.html',
  styleUrls: ['./expected-dates.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ExpectedDatesComponent implements OnInit {
  public readonly title: string = 'BOOKS.EXPECTED_DATES.TITLE';
  public form: FormGroup | undefined = undefined;
  public readonly inspectionDateInputLabel: string = 'BOOKS.EXPECTED_DATES.INSPECTION';
  public readonly shipmentDateInputLabel: string = 'BOOKS.EXPECTED_DATES.SHIPMENT';
  public readonly inspectionDateInputSize: EQimaInputSize = EQimaInputSize.MEDIUM;
  public readonly shipmentDateInputSize: EQimaInputSize = EQimaInputSize.MEDIUM;

  public constructor(private readonly _controlContainer: ControlContainer) {}

  public ngOnInit(): void {
    this.form = <FormGroup>this._controlContainer.control;
  }
}
