import { PreShipmentInspectionComponent } from '@aca-new/app/pages/book/pages/product-inspections/pages/pre-shipment-inspection/pre-shipment-inspection.component';
import { PreShipmentInspectionModule } from '@aca-new/app/pages/book/pages/product-inspections/pages/pre-shipment-inspection/pre-shipment-inspection.module';
import { AuthoritiesGuard } from '@aca-new/app/shared/guards/authorities/authorities.guard';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

const ROUTES: Routes = [
  {
    path: '',
    canActivate: [AuthoritiesGuard],
    children: [
      {
        path: '',
        redirectTo: 'psi',
        pathMatch: 'full',
      },
      {
        path: 'psi',
        component: PreShipmentInspectionComponent,
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(ROUTES), PreShipmentInspectionModule],
})
export class ProductInspectionsModule {}
